@import url('https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300;400;700&display=swap');

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Titillium Web', sans-serif;
}

:root {
  --mainColor: rgb(48, 48, 48);
  --mainColorLight: grey;
  --secondaryColor: rgb(19, 200, 255);
  --textColor: rgb(169, 126, 7);
  --textColor2: white;
}
header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;
  padding: 0 2rem;
  background-color: var(--mainColor);
  color: var(--textColor);
}
nav a{
  cursor: pointer;
  font-size: 2rem;
  margin: 0 0rem;
  color: var(--textColor);
  text-decoration: none;
  border: rbga(0, 0, 0, 0) .125em solid;
  padding: .3em 1em;
  border-radius: .25em;
}

nav a:hover{
  border-color: var(--secondaryColor);
  color: var(--textColor2);
  text-shadow:
  0 0 .25em hsl(0 0% 100% / 5), 
  0 0 .45em var(--secondaryColor);

  box-shadow: inset 0 0 .5em 0 var(--secondaryColor), 0 0 .5em 0 var(--secondaryColor);
}

.picture {
  height: 50px;
  width: 50px;
}

p1 {
  left: 50%;
  float: right;
  position: absolute;
  font-size: 1.5rem;
  padding: 2.5em;
  color: var(--textColor);
  align-items: center;
  display: inline;
  right: 0%;
}

p2 {
  position: absolute;
  font-size: 1.5rem;
  padding: 2.5em;
  color: var(--textColor);
  align-items: center;
  display: inline;
  float: left;
  bottom: 20%;
  right: 50%;
  left: 0%;
}

f {
  font-weight: bolder;
}

body {
  overflow: hidden;
  background-color: rgb(38, 38, 38);
}

